import Section from "../../components/Section/Section";
import Text from "../../components/Text/Text";
import Title from "../../components/Title/Title";
import Bubble from "../../components/Bubble/Bubble";
import "./Info.scss";
import LightBulb from "../../icons/LightBulb";
import Networking from "../../icons/Networking";
import Star from "../../icons/Star";
import Tools from "../../icons/Tools";
import { StructuredText } from "react-datocms";
import { useStaticElement } from "../../tools/datoCmsTools";

const Info = () => {
  const [infoText1] = useStaticElement("info1");
  const [infoText2] = useStaticElement("info2");

  return (
    <Section container placeholder id="info-section">
      <Title>
        Legynk ismt <span className="highlight">EGYTT</span>!
      </Title>
      <Text subtitle>
        <StructuredText data={infoText1} />
      </Text>

      <div className="row">
        <div className="col-md-6 col-12">
          <div className="squares">
            <div className="square blue large">
              <div className="inner">
                <div className="square-icon">
                  <LightBulb />
                </div>
                <div className="square-title">Inspirci</div>
              </div>
            </div>
            <div className="square">
              <div className="inner">
                <div className="square-title">Kzssgi kapcsolatok</div>
              </div>
            </div>
            <div className="square">
              <div className="inner">
                <div className="square-title">Versenykpes oktats</div>
              </div>
            </div>
            <div className="square purple">
              <div className="inner">
                <div className="square-title">Piackpes tuds</div>
              </div>
            </div>
          </div>
        </div>
        <div className="col-md-6 col-12">
          <Title subtitle>Tltdj fel a legfrissebb informcikkal!</Title>
          <Text>
            <StructuredText data={infoText2} />
          </Text>
        </div>
      </div>
    </Section>
  );
};

export default Info;
